.myListBg{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.myListWrap{
  border-radius: 0.5vw;
  max-width: 600px;
  width: 75%;
  height: 80vh;
  background-color: rgb(245, 245, 245);
  box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.1);
}
.myBoxWrap{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 100%;
  position: relative;
}
/*닉네임 변경 시작*/
.searchWrap{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 15%;
}
.searchWrap>div:nth-of-type(1){
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 70%;
  height: 70%;
}
.searchBox{
  background-color: rgb(255, 255, 255);
  border-radius: 0.5rem;
  width: 70%;
  padding: 1.3rem;
  font-size: 1.6rem;
  box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.1);
}
.searchBox::placeholder{
  color: #a6a6a6;
}
.searchBtn{
  box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.1);
  background-color: #333;
  color: #fff;
  cursor: pointer;
  font-size: 1.6rem;
  width: 20%;
  padding: 1.3rem;
  border-radius: 0.5rem;
}
.searchBtn:hover{
  background-color: #505050;
}
/*닉네임 변경 끝*/

/*닉네임&친구요청 시작*/
.myNicButtonWrap{
  width: 100%;
  height: 15%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.myNicButtonWrap>div:nth-of-type(1){
  width: 80%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.nicZone{
  width: 50%;
}
.myNickname{
  width: 100%;
  font-size: 2.5rem;
  color: #333;
}
.friendBtnZone{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 40%;
}
.addFriendBtn{
  width: 5rem;
  height: 5rem;
  background-color: #333;
  border-radius: 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.requestFriendBtn{
  width: 5rem;
  height: 5rem;
  background-color: #333;
  border-radius: 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

Button[class$="Btn"]>img{
  display: block;
  width: 40%;
}
/*닉네임&친구요청 끝*/

/*친구 목록 시작*/
.friendListWrap{
  width: 100%;
  height: 70%;
  overflow-y: scroll;
}
/*친구 목록 끝*/

/*모바일 시작*/
@media (max-width:600px){
  .myListWrap{
    border-radius: 0;
    max-width: none;
    width: 100%;
    height: 100%;
    background-color: rgb(255, 255, 255);
  }
}
/*모바일 끝*/