.fFlexWrap{
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.fListStyle{
  width: 100%;
}
.fListStyle>div:nth-of-type(1){
  margin-top: 0.3rem;
  padding: 0.5rem;
  outline: 1px solid #00000015;
}
.fListStyle>div:nth-of-type(1)>p{
  font-size: 1.3rem;
  color: #333;
}
.fListStyle>div:nth-of-type(2){
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.fListStyle>div:nth-of-type(2)>div{
  outline: 1px solid #e1e1e1;
  background-color: rgb(255, 255, 255);
  overflow: hidden;
  border-radius: 1rem;
  width: 96%;
  height: 10rem;
  margin: 1rem 0 1rem 0;
}
.friendInfoZone{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
}
/*친구 프로필 시작*/
.profileZone{
  overflow: hidden;
  width: 20%;
  height: 100%;
  position: relative;
}
.profileBox{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  width: 7rem;
  height: 7rem;
  background-color: rgb(137, 137, 137);
  overflow: hidden;
}
.profileBox>img{
  margin-left: 1rem;
  margin-top: 1rem;
  width: 100%;
}
.connectionState{
  border-radius: 50%;
  width: 2.5rem;
  height: 2.5rem;
  position: absolute;
  top: 65%;
  left: 65%;
  background-color: aquamarine;
}
/*친구 프로필 끝*/
/*친구 닉네임 메모 시작*/
.nameMemoZone{
  margin-left: 2%;
  width: 43%;
  height: 60%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}
.fNicZone{
  width: 100%;
}
.fNicZone>p{
  font-weight: bold;
  width: 80%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 2rem;
  color: #333;
}
.fMemoZone{
  width: 100%;
}
.fMemoZone>p{
  width: 80%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 1.5rem;
  color: #00000082;
}
/*친구 닉네임 메모 끝*/
/*마지막 접속 친구된 날 시작*/
.registLastZone{
  margin-right: 2%;
  width: 33%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
}
.registLastZone>div:nth-of-type(1){
  padding: 0.5rem;
}
.registLastZone>div:nth-of-type(1)>p{
  font-size: 1.3rem;
  color: #00000072;
}
.registLastZone>div:nth-of-type(2){
  padding: 0.5rem;
}
.registLastZone>div:nth-of-type(2)>p{
  font-size: 1.1rem;
  color: #00000055;
}
/*마지막 접속 친구된 날 끝*/