.popupWrap{
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 10;
  background-color: #0000003f;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.popupWrap>div{
  width: 60%;
  height: 30%;
  background-color: #fff;
  border-radius: 0.3rem;
  overflow: hidden;
  box-shadow: 1px 2px 5px #0000000e;
}
.popupWrap>div>div:nth-of-type(1){
  width: 100%;
  height: 4rem;
  background-color: #828282;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: relative;
}
.popupWrap>div>div:nth-of-type(1)>p{
  color: #fff;
  width: 100%;
  text-align: center;
  font-size: 1.9rem;
}
.popupWrap>div>div:nth-of-type(1)>div{
  position: absolute;
  cursor: pointer;
  border-radius: 0.2rem;
  right: 2%;
  width: 3rem;
  height: 3rem;
  background-color: #333;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.popupWrap>div>div:nth-of-type(1)>div>img{
  width: 70%;
  opacity: 0.7;
  display: block;
}
.popupWrap>div>div:nth-of-type(2){
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.mesagePop{
  height: 25%!important;
}
.popupWrap>div>div:nth-of-type(2)>img{
  width: 6rem;
  height: 6rem;
  margin: -2.8rem 0 1rem 0;
  display: block;
  opacity: 0.8;
}
.errImg{
  margin: -4rem 0 1rem 0!important;
}
.popupWrap>div>div:nth-of-type(2)>p{
  width: 90%;
  font-size: 1.5rem;
  text-align: center;
  color: #333;
}
.popupWrap>div>div:nth-of-type(2)>p:nth-of-type(2){
  color: #805050;
}
.popupWrap>div>div:nth-of-type(2)>input{
  outline: 1px solid #333;
  margin-top:2rem;
  width: 80%;
  text-align: center;
  height: 3rem;
  font-size: 1.5rem;
  padding: 0.3rem;
  border-radius: 0.3rem;
}
.popupWrap>div>div:nth-of-type(2)>button{
  padding: 1rem;
  margin: 2rem;
  border-radius: 0.3rem;
  font-size: 1.4rem;
  cursor: pointer;
  background-color: #333;
  color: #fff;
}
.acceptRefuseBtnWrap{
  width: 80%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.acceptRefuseBtnWrap>button{
  padding: 1rem;
  margin: 2rem;
  border-radius: 0.3rem;
  font-size: 1.4rem;
  cursor: pointer;
  background-color: #333;
  color: #fff;
}